$(document).ready(function() {

        $("#navToggle").click(function() {
                $(this).toggleClass("active");
                $(".overlay").toggleClass("open");
        });

        $('.overlay').click(function() {
                $(this).removeClass('open');
                $('.navBurger').removeClass('active');
        });

        $('.select-lang').click(function() {
                if ($("select#select-language :selected").val() == "ru") {
                        $("#flag").removeClass();
                        $("#flag").addClass('flag-icon flag-icon-ru flag-icon-squared flag');
                } else if ($("select#select-language :selected").val() == "eng") {
                        $("#flag").removeClass();
                        $("#flag").addClass('flag-icon flag-icon-us flag-icon-squared flag');
                } else if ($("select#select-language :selected").val() == "ua") {
                        $("#flag").removeClass();
                        $("#flag").addClass('flag-icon flag-icon-ua flag-icon-squared flag');
                }
        });
})